import {HIDE_MODAL, SHOW_MODAL} from '../actions/modalActions';

const modalReducer = (state = {value: 0}, action) => {
    switch (action.type) {
        case SHOW_MODAL:
            return {...state, value: true};
        case HIDE_MODAL:
            return {...state, value: 'hide'};
        default:
            return {...state};
    }
};

export default modalReducer;